@Component({
    selector: 'fc-verifycode',
    templateUrl: './fcverifycode.component.html',
    styles: [
        `
        .fc-verifycode {
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            position: relative;
        }
        .fc-verifycode input {
            width: calc(100% - 110px);
        }
        .fc-verifycode canvas{
            border: 1px solid #d9d9d9;
            border-radius: 4px;
            cursor: pointer;
        }
        .fc-verifycode  .fc-form-error {
            position: absolute;
            left: 0;
            top: 32px;
            font-size: 12px;
            color: #f5222d;
        }
        .fc-verifycode  .fc-form-error input{
            background-color: #fff;
            border-color: #f5222d;
        }
        `
    ]
})
export class Fcverifycode implements OnInit {
    @ViewChild('verifyCode', { static: true }) canvasRef: ElementRef;
    // 
    @Input()
    fcCode: Array<number>;
    // 
    validVerifyCode: string;
    // 
    public fcShowError: string = 'Y';
    Constructor() {

    }
    ngOnInit(): void {
        this.draw([]);
        console.log(this.validVerifyCode)
    }
    //
    draw(show_num) {
        let elem = this.canvasRef.nativeElement;
        console.dir(elem);
        // let canvas_width = elem.offsetWidth;
        // let canvas_height = elem.offsetHeight;
        let canvas_width = 100;
        let canvas_height = 30;
        let canvas = elem;//canvas
        let context = canvas.getContext("2d");//canvas
        canvas.width = canvas_width;
        canvas.height = canvas_height;
        let sCode = "a,b,c,d,e,f,g,h,i,j,k,m,n,p,q,r,s,t,u,v,w,x,y,z,A,B,C,E,F,G,H,J,K,L,M,N,P,Q,R,S,T,W,X,Y,Z,1,2,3,4,5,6,7,8,9,0";
        let aCode = sCode.split(",");
        let aLength = aCode.length;//

        for (let i = 0; i < 4; i++) {  //for646
            let j = Math.floor(Math.random() * aLength);//
            // var deg = Math.random() * 30 * Math.PI / 180;//0~30
            let deg = Math.random() - 0.5; //
            let txt = aCode[j];//
            show_num[i] = txt.toLowerCase();
            let x = 10 + i * 20;//canvasx
            let y = 20 + Math.random() * 8;//canvasy
            context.font = "bold 23px ";

            context.translate(x, y);
            context.rotate(deg);

            context.fillStyle = this.randomColor();
            context.fillText(txt, 0, 0);

            context.rotate(-deg);
            context.translate(-x, -y);
        }
        for (let i = 0; i <= 5; i++) { //
            context.strokeStyle = this.randomColor();
            context.beginPath();
            context.moveTo(Math.random() * canvas_width, Math.random() * canvas_height);
            context.lineTo(Math.random() * canvas_width, Math.random() * canvas_height);
            context.stroke();
        }
        for (let i = 0; i <= 30; i++) { //
            context.strokeStyle = this.randomColor();
            context.beginPath();
            let x = Math.random() * canvas_width;
            let y = Math.random() * canvas_height;
            context.moveTo(x, y);
            context.lineTo(x + 1, y + 1);
            context.stroke();
        }

        this.validVerifyCode = show_num.join('');
    }

    //
    randomColor() {
        let r = Math.floor(Math.random() * 256);
        let g = Math.floor(Math.random() * 256);
        let b = Math.floor(Math.random() * 256);
        return "rgb(" + r + "," + g + "," + b + ")";
    }
    /**
     * 
     */
    changeValue(event) {
        if (event.toLowerCase() === this.validVerifyCode.toLowerCase()) {
            this.fcShowError = 'N';
        }
    }
}
